.lazy-image-container {
  position: relative;
  -ms-flex-item-align: end;
      align-self: flex-end;
  width: 100%;
  height: 50vh;

  overflow: hidden;
}

.lazy-image-preload {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.lazy-image-loaded {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-size: cover;

  opacity: 0;

  -webkit-transition: opacity 1s ease;

  -o-transition: opacity 1s ease;

  transition: opacity 1s ease;
}

.lazy-image-fade-in {
  opacity: 1;
}

.lazy-image-fade-out {
  opacity: 0;
}


